      border-radius: 50%;
      width: 40px;
      height: 40px;
      animation: spin 1s linear infinite; /* Infinite spinning */
  }

  @keyframes spin {
      0% {
          transform: rotate(0deg);
      }
      100% {
          transform: rotate(360deg);
      }
  }

  @keyframes pulse-green {
      0% {
          box-shadow: 0 8px 32px rgba(0,0,0,0.25), 0 0 0 0 rgba(20, 174, 92, 0.7);
      }
      70% {
          box-shadow: 0 8px 32px rgba(0,0,0,0.25), 0 0 0 10px rgba(20, 174, 92, 0);
      }
      100% {
          box-shadow: 0 8px 32px rgba(0,0,0,0.25), 0 0 0 0 rgba(20, 174, 92, 0);
      }
  }

  @keyframes pulse-red {
      0% {
          box-shadow: 0 8px 32px rgba(0,0,0,0.25), 0 0 0 0 rgba(236, 34, 31, 0.7);
      }
      70% {
          box-shadow: 0 8px 32px rgba(0,0,0,0.25), 0 0 0 10px rgba(236, 34, 31, 0);
      }
      100% {
          box-shadow: 0 8px 32px rgba(0,0,0,0.25), 0 0 0 0 rgba(236, 34, 31, 0);
      }
  }
  .chat-container {
    height: 300px; /* Adjust based on your popup size */
    width: 100%; /* Full width on mobile */
    max-width: 400px; /* Max width on larger screens */
