      max-width: 90%; /* Allow bubbles to take more width on mobile */
    }
  }
  


#root {
    max-width: 1280px;
    margin: 0 auto;
    padding: 2rem;
    text-align: center;
  }
  
  .logo {
    height: 6em;
    padding: 1.5em;
    will-change: filter;
    transition: filter 300ms;
  }
  .logo:hover {
    filter: drop-shadow(0 0 2em #646cffaa);
  }
  .logo.react:hover {
    filter: drop-shadow(0 0 2em #61dafbaa);
  }
  
  @keyframes logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  @media (prefers-reduced-motion: no-preference) {
    a:nth-of-type(2) .logo {
      animation: logo-spin infinite 20s linear;
    }
  }
  
  .card {
    padding: 2em;
  }
  
  .read-the-docs {
    color: #888;
  }
  

/* widget */

.chat-widget {
  display: flex;
  align-items: center;
  background-color: #002F86;
  color: white;
  padding: 12px 24px;
  border-radius:16px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
  font-family: 'Orbitron', sans-serif;
  gap: 1px;
  width: fit-content;
}

.chat-logo {
  width: 50px;  /* Adjusted width */
  height: 50px;  /* Adjusted height */
  border-radius: 50%;  /* Make it a circle */
  overflow: hidden;  /* Ensures the image is clipped to fit the circle */
}

.chat-logo-img {
  width: 100%;  /* Make the image responsive within the container */
  height: 100%;
  object-fit: cover;  /* Ensures the image covers the div while maintaining aspect ratio */
}

.chat-text {
  /* display: flex; */
  flex-direction: column;
  color: white !important;
  /* text-align: left; */
}
.chat-text-header{
   text-align: center !important;
   color: white !important;
}

.chat-text p {
  color: white !important;
  margin: 0;
  font-size: 12px;
  /* font-weight: bold; */
}

.underline {
  display: inline-block;
  width: 80px;
  border-bottom: 2px solid white;
  margin-left: 5px;
}

.chat-powered {
  font-size: 2px;
  color: white;
  padding-left:38px;
